@import url(https://fonts.googleapis.com/css2?family=Karla:wght@200;300&family=Rubik:wght@800&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.app {
  background: cornflowerblue;
}

.site-layout-content {
  min-height: 280px;
  padding: 0 24px;
  background: #fff;
  font-family: "Karla", sans-serif;
  font-size: large;
  font-weight: 300;
}

.page-body {
  padding: 20px 10% 0;
}


.title {
  float: left;
  margin: 0 24px 16px 0;
}

.title .title-text {
  color: #fff;
  font-family: "Rubik", sans-serif;
  font-size: large;
}

.heading-main {
  font-family: "Rubik", sans-serif;
  font-size: xxx-large;
}



/* .main-text {
  padding: 10px;
} */
.Home {
  margin-bottom: 50px;
}

.box {
  width: 100px;
  height: 100px;
  background: #000;
}

.carousel-image {
  height: 400px;
  width: 100%;
  object-fit: cover;
}




